import { Part as PartClass } from '../Part.js';

export class Scanner extends PartClass {
    name = "Scanner";
    description = "Des outils de spectromtrie indiquant la nature de plantes avoisinantes.";
    levels = [{
        level : 1,
        text : "Affiche la nature des plantes de l'tape suivante",
        price : 50,
    },{
        level : 2,
        text : "Affiche la nature des plantes des 2 tapes suivantes",
        price : 25,
    },{
        level : 3,
        text : "Affiche la nature des plantes des 3 tapes suivantes",
        price : 50,
    },{
        level : 4,
        text : "Affiche la nature des plantes des 4 tapes suivantes",
        price : 75,
    },{
        level : 5,
        text : "Affiche la nature des plantes des 5 tapes suivantes",
        price : 100,
    }];

    constructor(level) {
        super(level);
    }

    scan = function (System) {
        for (let i = 1; i <= this.level; i++) {
            if (System.game.planet.step + i < System.game.sector.steps.length) {
                let step = System.game.sector.steps[System.game.planet.step + i];
                for (let j = 0; j < step.length; j++) {
                    step[j].info = true;
                }
            }
        }
    }

    upgradeEffect = function (System) {
        this.scan(System)
    }
} 